@import "node_modules/bootstrap/scss/variables";
$enable-flex: true;
@import "node_modules/bootstrap/scss/mixins";
@import "node_modules/bootstrap/scss/normalize";
@import "node_modules/bootstrap/scss/reboot";
@import "node_modules/bootstrap/scss/type";
@import "node_modules/bootstrap/scss/grid";
@import "node_modules/bootstrap/scss/utilities";
header{
    height: 60vh;
    background-image: url(../../images/header.jpg);
    background-position: center;
    background-size: cover;
}
footer{
    height: 25vh;
    background-color: darken(#21b7dc,30);
}
.employees {
    &:hover {
        .img {
            &:before {
                background-color: rgba(#fff, .6);
            }
        }
    }
    .img {
        background-position: center;
        background-size: cover;
        margin: 1rem;
        &:before {
            content: '';
            position: absolute;
            background-color: rgba(#fff, 0);
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            transition: background-color 350ms ease-in-out;
        }
        .desc-wrapper {
            position: absolute;
            bottom: 0;
            width: 100%;
            transform-origin: bottom;
            perspective-origin: center;
            transform: perspective(450px) translate3d(0, 0, 0);
            transform-style: preserve-3d;
            z-index: 1;
            pointer-events: none;
            transition: transform 350ms ease-in-out;
        }
        .desc {
            position: absolute;
            background-color: #21b7dc;
            padding: 20px;
            color: #fff;
            margin: 0 -1rem;
            transform: rotate3d(1, 0, 0, -90deg);
            transform-origin: top;
            transition: transform 350ms ease-in-out;
            h4,
            p {
                opacity: 0;
                transition: opacity 350ms ease-in-out 200ms;
            }
        }
        &:hover {
            &:before {
                background-color: rgba(#fff, 0);
            }
            .desc-wrapper {
                pointer-events: all;
                transform: perspective(450px) translate3d(0, -60px, 0);
            }
            .desc {
                transform: rotate3d(1, 0, 0, 0deg);
                h5 {
                    opacity: 1;
                }
                p {
                    opacity: 1;
                }
            }
        }
    }
}

.box {
    position: relative;
    width: 100%;
    /* desired width */
    >div {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
    }
}

.box:before {
    content: "";
    display: block;
    padding-top: 100%;
    /* initial ratio of 1:1*/
}
