header {
  height: 60vh;
  background-image: url(../../images/header.jpg);
  background-position: center;
  background-size: cover; }

footer {
  height: 25vh;
  background-color: #0d4857; }

.employees:hover .img:before {
  background-color: rgba(255, 255, 255, 0.6); }

.employees .img {
  background-position: center;
  background-size: cover;
  margin: 1rem; }
  .employees .img:before {
    content: '';
    position: absolute;
    background-color: rgba(255, 255, 255, 0);
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    transition: background-color 350ms ease-in-out; }
  .employees .img .desc-wrapper {
    position: absolute;
    bottom: 0;
    width: 100%;
    transform-origin: bottom;
    perspective-origin: center;
    transform: perspective(450px) translate3d(0, 0, 0);
    transform-style: preserve-3d;
    z-index: 1;
    pointer-events: none;
    transition: transform 350ms ease-in-out; }
  .employees .img .desc {
    position: absolute;
    background-color: #21b7dc;
    padding: 20px;
    color: #fff;
    margin: 0 -1rem;
    transform: rotate3d(1, 0, 0, -90deg);
    transform-origin: top;
    transition: transform 350ms ease-in-out; }
    .employees .img .desc h4,
    .employees .img .desc p {
      opacity: 0;
      transition: opacity 350ms ease-in-out 200ms; }
  .employees .img:hover:before {
    background-color: rgba(255, 255, 255, 0); }
  .employees .img:hover .desc-wrapper {
    pointer-events: all;
    transform: perspective(450px) translate3d(0, -60px, 0); }
  .employees .img:hover .desc {
    transform: rotate3d(1, 0, 0, 0deg); }
    .employees .img:hover .desc h5 {
      opacity: 1; }
    .employees .img:hover .desc p {
      opacity: 1; }

